import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const userKey = 'user'
const tokenKey = 'token'
const store = new Vuex.Store({
  state: {
    isLogin: false,
    user: null,
    token: null
  },
  getters: {
    isLogin: function (state) {
      if (!state.isLogin) {
        state.isLogin = (localStorage.getItem(userKey) !== null) && (localStorage.getItem(tokenKey) !== null)
      }
      return state.isLogin
    },
    getUser: function (state) {
      if (!state.user) {
        state.user = JSON.parse(localStorage.getItem(userKey))
      }
      return state.user
    },
    getToken: function (state) {
      if (!state.token) {
        state.token = JSON.parse(localStorage.getItem(tokenKey))
      }
      return state.token
    }
  },
  mutations: {
    $_setStorage (state, value) {
      state.isLogin = true
      state.user = value.user
      state.token = value.token
      localStorage.setItem(userKey, JSON.stringify(value.user))
      localStorage.setItem(tokenKey, JSON.stringify(value.token))
    },
    $_removeStorage (state) {
      state.isLogin = false
      state.user = null
      state.token = null
      localStorage.removeItem(userKey)
      localStorage.removeItem(tokenKey)
    }
  }
})

export default store



