<template>
  <div id="app">
    <nav class="navbar navbar-expand-sm navbar-expand-lg bg-purple">
        <a class="navbar-brand" href="/">
            <img src="./assets/icon/logo.svg" width="24" height="24" class="d-inline-block align-top" alt="" loading="lazy"> Home
        </a>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarSupportedContent">
            <ul class="navbar-nav mr-auto">
            </ul>
            <ul class="navbar-nav">
              <template v-if="this.$store.getters.isLogin === false">
                <!-- <li :class="{'nav-item': true, 'active': SigninActiveCtrl}" id="nav-signin"> -->
                <li class="nav-item" id="nav-signin">
                    <a class="nav-link" href="/user/signin" data-toggle="tooltip" data-placement="bottom" title="Sign in">Sign in</a>
                </li>
                <!-- <li :class="{'nav-item': true, 'active': SignupActiveCtrl}" id="nav-signup"> -->
                <li class="nav-item" id="nav-signup">
                    <a class="nav-link" href="/user/signup" data-toggle="tooltip" data-placement="bottom" title="Sign up">Sign up</a>
                </li>
              </template>
              <template v-else>
                <li class="dropdown nav-item">
                    <a class="nav-link" href role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        <img width="23" height="23" class="header-user-avatar" src="https://secure.gravatar.com/avatar/1092bff64b134e966d0a8b513c9c4168?s=46&amp;d=identicon">
                        <svg class="caret-down" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16" id="angle-down">
                            <path fill-rule="evenodd" d="M12.293 5.293a1 1 0 0 1 1.414 1.414l-5 5a1 1 0 0 1-1.414 0l-5-5a1 1 0 0 1 1.414-1.414L8 9.586l4.293-4.293z"></path>
                        </svg>
                    </a>
                    <div class="dropdown-menu dropdown-menu-right">
                        <h6 class="dropdown-header">@{{ this.$store.getters.getUser }}</h6>
                        <div class="dropdown-divider"></div>
                        <a class="dropdown-item" @click="logout">Sign out</a>
                    </div>
                </li>
              </template>
            </ul>
        </div>
    </nav>
    <router-view v-wechat-title='$route.meta.title'></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      SigninActiveCtrl: false,
      SignupActiveCtrl: false
    }
  },
  mounted () {
    $("[data-toggle='tooltip']").tooltip()
    // 解决bootstrap下拉菜单第一次点击无反应问题
    $("[data-toggle='dropdown']").dropdown()
    let url = window.location.pathname
    // let url = this.$route.path
    this.SigninActiveCtrl = url === '/user/signin'
    this.SignupActiveCtrl = url === '/user/signup'
    // 上面使用 MVVM 模型在刷新的时候有点问题
    if (url === '/user/signin') {
      $('#nav-signin').addClass('active')
    } else if (url === '/user/signup') {
      $('#nav-signup').addClass('active')
    }
    console.log(this.$store.getters.getUser, this.$store.getters.getToken)
  },
  methods: {
    logout: function () {
      let _this = this
      $.ajax({
        url: '/api/user/logout',
        type: 'POST',
        headers: { 'X-CSRFToken': $.cookie('csrftoken') },
        data: JSON.stringify(),
        dataType: 'JSON',
        processData: false,
        contentType: 'application/json; charset=utf-8',
        success: function (args) {
          console.log(args.status, args.msg)
          if (args.status) {
            _this.$store.commit('$_removeStorage')
            // _this.$router.push('/')
            window.location.href = '/'
          } else {
            console.log('Error occured when logout')
          }
        }
      })
    }
  }
}
</script>

<style scoped src="./assets/css/nav.css">

</style>



