<template>
    <div class="signin-container">
        <div class="signin-header">
            <span>Sign in to Home</span>
        </div>
        <div class="signin-flash-error" id="flash-error" v-if="flashVisualCtrl === true">
            <span class="signin-error-message">Incorrect username or password.</span>
            <button class="signin-flash-close" type="button" @click="hideFlash">
                <svg class="signin-octicon" viewBox="0 0 12 16" version="1.1" width="12" height="16" aria-hidden="true">
                    <path fill-rule="evenodd" d="M7.48 8l3.75 3.75-1.48 1.48L6 9.48l-3.75 3.75-1.48-1.48L4.52 8 .77 4.25l1.48-1.48L6 6.52l3.75-3.75 1.48 1.48L7.48 8z"></path>
                </svg>
            </button>
        </div>
        <!-- <div class="signin-tab-box">
            <label class="signin-tab-body">Sign in to Home</label>
        </div> -->
        <div class="signin-box">
            <div class="signin-body">
                <div class="signin-form-group">
                    <label for="username" class="signin-username">
                        <span class="signin-label-bold">Username</span>
                        <a class="signin-float-right" href="/user/signup">Don't have an account?</a>
                    </label>
                    <input class="form-control signin-form-control" autofocus="autofocus" autocapitalize="off" autocorrect="off" type="text" spellcheck="false" tabindex="1" v-model="username" id="username"/>
                </div>
                <div class="signin-form-group">
                    <label for="password" class="signin-password">
                        <span class="signin-label-bold">Password</span>
                        <a class="signin-float-right" href="">Forgot your password?</a>
                    </label>
                    <input class="form-control signin-form-control" autofocus="autofocus" type="password" tabindex="2" v-model="password" id="password"/>
                </div>
                <div class="signin-summit-container">
                    <input class="signin-btn" type="button" :disabled="signinEnCtrl === false" v-model="signinValueCtrl" tabindex="3" id="signin" @click="submit" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'signin',
  data () {
    return {
      signinEnCtrl: true,
      signinValueCtrl: 'Sign in',
      flashVisualCtrl: false,
      username: '',
      password: ''
    }
  },
  mounted () {
    let _this = this
    $(document).keydown(function (event) {
      if (event.keyCode === 13) {
        if ($('#username').is(':focus') || $('#password').is(':focus') || $('#signin').is(':focus')) {
          _this.submit()
          return false
        }
      }
      return true
    })
  },
  methods: {
    submit: function () {
      let formData = {
        'username': this.username,
        'password': this.password
      }
      let _this = this
      $.ajax({
        url: '/api/user/signin',
        type: 'POST',
        headers: { 'X-CSRFToken': $.cookie('csrftoken') },
        data: JSON.stringify(formData),
        dataType: 'JSON',
        processData: false,
        contentType: 'application/json; charset=utf-8',
        success: function (args) {
          console.log(args.status, args.msg)
          if (args.status) {
            // _this.getToken()
            _this.$store.commit('$_setStorage', args)
            window.location.href = '/'
            // _this.$router.push('/')
          } else {
            _this.flashVisualCtrl = true
            _this.signinEnCtrl = true
            _this.signinValueCtrl = 'Sign in'
            _this.password = ''
          }
        }
      })
      _this.signinEnCtrl = false
      _this.signinValueCtrl = 'Signing in...'
    },
    hideFlash: function () {
      this.flashVisualCtrl = false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped src="../assets/css/signin.css">

</style>



