<template>
    <div class="signup-container">
        <div class="signup-flash-success" v-if="false" id="flash-box">
            <span class="signup-flash-message" id="flash-messag">Sign up successfully! </span>
            <a href="/user/signin">Sign in now.</a>
            <button class="signup-flash-close" type="button" id="del-flash-message">
                <svg class="signup-octicon" viewBox="0 0 12 16" version="1.1" width="12" height="16" aria-hidden="true">
                    <path fill-rule="evenodd" d="M7.48 8l3.75 3.75-1.48 1.48L6 9.48l-3.75 3.75-1.48-1.48L4.52 8 .77 4.25l1.48-1.48L6 6.52l3.75-3.75 1.48 1.48L7.48 8z"></path>
                </svg>
            </button>
        </div>
        <div class="signup-tab-box">
            <span class="signup-tab-body">Create your account</span>
        </div>
        <div class="signup-box">
            <div class="signup-body">
                <div class="signup-form-group">
                    <label for="username" class="signup-username">
                        <span class="signup-label-bold">Username</span>
                        <span class="signup-username-message" id="username-message" v-show="usrMsgVisualCtrl" v-html="usrMsgValueCtrl"></span>
                    </label>
                    <input :class="{'form-control': true, 'signup-form-control': true, 'is-autocheck-errored': inputErrCtrl, 'is-autocheck-success': inputSucCtrl, 'is-autocheck-loading': inputLoadCtrl}" autofocus="autofocus" autocapitalize="off" autocorrect="off" required="required" type="text" spellcheck="false" tabindex="1" id="username" v-on:input="usernameChanged" v-model="username"/>
                    <span class="siginup-username-hint">Username may only contain numbers or letters.<br>Length required to be 5 to 39 characters.</span>
                </div>
                <div class="signup-form-group">
                    <label for="password" class="signup-password">
                        <span class="signup-label-bold">Password</span>
                        <span class="signup-password-message" id="password-message" v-show="pwdMsgVisualCtrl" v-html="pwdMsgValueCtrl"></span>
                    </label>
                    <input class="form-control signup-form-control" autofocus="autofocus" required="required" type="password" tabindex="2" id="password" v-on:input="passwordChanged" v-model="password"/>
                    <span class="siginup-password-hint">Minimum length is 8 characters.</span>
                </div>
                <div class="signup-summit-container">
                    <input class="signup-btn" type="button" :disabled="signupEnCtrl === false" v-model="signupValueCtrl" tabindex="3" id="signup" @click="submit"/>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'signup',
  data () {
    return {
      inputLoadCtrl: false,
      inputErrCtrl: false,
      inputSucCtrl: false,
      usrMsgVisualCtrl: false,
      usrMsgValueCtrl: '',
      pwdMsgVisualCtrl: false,
      pwdMsgValueCtrl: '',
      isUsernameValid: false,
      isPasswordValid: false,
      signupEnCtrl: false,
      signupValueCtrl: 'Sign up',
      username: '',
      password: ''
    }
  },
  mounted () {
    let _this = this
    $(document).keydown(function (event) {
      if (event.keyCode === 13) {
        if (($('#username').is(':focus') || $('#password').is(':focus') || $('#signup').is(':focus')) && (_this.signupEnCtrl === true)) {
          _this.submit()
          return false
        }
      }
      return true
    })
  },
  methods: {
    showNothing: function () {
      this.inputLoadCtrl = false
      this.inputErrCtrl = false
      this.inputSucCtrl = false
    },
    showLoading: function () {
      this.showNothing()
      this.inputLoadCtrl = true
    },
    showError: function () {
      this.showNothing()
      this.inputErrCtrl = true
    },
    showSuccess: function () {
      this.showNothing()
      this.inputSucCtrl = true
    },
    usernameChanged: function () {
      let pattern = /^[a-zA-Z0-9]+$/
      this.usrMsgVisualCtrl = false
      this.isUsernameValid = false
      this.signupEnCtrl = false
      this.showLoading()
      if (this.username === '') {
        this.showNothing()
        return
      }
      if (pattern.test(this.username) === false) {
        this.showError()
        this.usrMsgValueCtrl = 'Username is not available.'
        this.usrMsgVisualCtrl = true
        return
      }
      if (this.username.length < 5 || this.username.length > 39) {
        this.showError()
        this.usrMsgValueCtrl = 'Username is not available.'
        this.usrMsgVisualCtrl = true
        return
      }
      let formData = {
        'action': 0,
        'username': this.username
      }
      let _this = this
      $.ajax({
        url: '/api/user/signup',
        type: 'POST',
        headers: { 'X-CSRFToken': $.cookie('csrftoken') },
        data: JSON.stringify(formData),
        dataType: 'JSON',
        processData: false,
        contentType: 'application/json; charset=utf-8',
        success: function (args) {
          console.log(args.status, args.msg)
          if (args.status) {
            _this.showSuccess()
            _this.isUsernameValid = true
            if (_this.isUsernameValid && _this.isPasswordValid) {
              _this.signupEnCtrl = true
            }
          } else {
            _this.showError()
            _this.usrMsgValueCtrl = 'Username has been taken.'
            _this.usrMsgVisualCtrl = true
            _this.isUsernameValid = false
            _this.signupEnCtrl = false
          }
        }
      })
    },
    passwordChanged: function () {
      this.pwdMsgVisualCtrl = false
      this.isPasswordValid = false
      this.signupEnCtrl = false
      if (this.password === '') {
        return
      }
      if (this.password.length < 8 || this.password.length > 128) {
        this.pwdMsgValueCtrl = 'Password is not available.'
        this.pwdMsgVisualCtrl = true
        return
      }
      this.isPasswordValid = true
      if (this.isUsernameValid && this.isPasswordValid) {
        this.signupEnCtrl = true
      }
    },
    submit: function () {
      let formData = {
        'action': 1,
        'username': this.username,
        'password': this.password
      }
      // let _this = this
      $.ajax({
        url: '/api/user/signup',
        type: 'POST',
        headers: { 'X-CSRFToken': $.cookie('csrftoken') },
        data: JSON.stringify(formData),
        dataType: 'JSON',
        processData: false,
        contentType: 'application/json; charset=utf-8',
        success: function (args) {
          if (args.status) {
            alert("Sign up successfully! now it's going to sign in.")
            // _this.$router.push('/user/signin')
            window.location.href = '/user/signin'
          } else {
          }
        }
      })
      this.signupEnCtrl = false
      this.signupValueCtrl = 'Signing up...'
    },
    hideFlash: function () {
      // $('#flash-box').hide()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped src="../assets/css/signup.css">

</style>



