<template>
    <div class="home-date-container">
        <div class="home-flash-container" v-if="flashVisualCtrl === true">
            <span class="home-flash-message" v-html="message"></span>
            <button class="home-flash-close" type="button" @click="hideFlash">
                <svg class="home-octicon" viewBox="0 0 12 16" version="1.1" width="12" height="16" aria-hidden="true">
                    <path fill-rule="evenodd" d="M7.48 8l3.75 3.75-1.48 1.48L6 9.48l-3.75 3.75-1.48-1.48L4.52 8 .77 4.25l1.48-1.48L6 6.52l3.75-3.75 1.48 1.48L7.48 8z"></path>
                </svg>
            </button>
        </div>
        <div class="home-date-body">
            <div class="home-form-group">
                <label for="date" class="home-date">
                  <span class="home-label-bold">Date</span>
                  <span class="home-date-message" v-show="isDateValid === false">Date is not available</span>
                </label>
                <input :class="{'form-control': true, 'home-form-control': true, 'is-autocheck-errored': inputErrCtrl, 'is-autocheck-success': inputSucCtrl}" autofocus="autofocus" required="required" type="text" spellcheck="false" id="date" v-on:input="formatCheck" v-model="dateInput"/>
                <span class="home-date-hint">
                  format required to be like <span class="hint-bold">YYYY-MM-DD</span>
                </span>
            </div>
            <input class="home-btn" type="button" v-model="btnValueCtrl" @click="submit" :disabled="btnEnableCtrl === false" id="dateCheck" />
        </div>
    </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      inputErrCtrl: false,
      inputSucCtrl: false,
      dateInput: '',
      isDateValid: true,
      btnEnableCtrl: true,
      btnValueCtrl: '查询',
      flashVisualCtrl: false,
      message: ''
    }
  },
  mounted () {
    let _this = this
    $(document).keydown(function (event) {
      if (event.keyCode === 13) {
        if (($('#date').is(':focus') || $('#dateCheck').is(':focus')) && (_this.btnEnableCtrl === true)) {
          _this.submit()
          return false
        }
      }
      return true
    })
  },
  methods: {
    dateHandler: function () {
      if (this.dateInput === '' || this.isDateValid === false) {
        this.message = 'Format required to be YYYY-MM-DD.'
        this.flashVisualCtrl = true
        this.btnEnableCtrl = true
        this.btnValueCtrl = '查询'
        return
      }
      let _this = this
      let formData = {
        'date': _this.dateInput
      }
      $.ajax({
        url: '/api/home',
        type: 'POST',
        headers: {
          'X-CSRFToken': $.cookie('csrftoken')
        },
        data: JSON.stringify(formData),
        dataType: 'JSON',
        processData: false,
        contentType: 'application/json; charset=utf-8',
        success: function (args) {
          console.log(args)
          if (args.status) {
            _this.message = args.msg
            _this.flashVisualCtrl = true
          } else {
            _this.message = 'Nothing happened this day.'
            _this.flashVisualCtrl = true
          }
          _this.btnEnableCtrl = true
          _this.btnValueCtrl = '查询'
        }
      })
    },
    formatCheck: function () {
      let pattern = /^[0-9]{4}[-][0-9]{2}[-][0-9]{2}$/
      if (this.dateInput === '') {
        this.inputErrCtrl = false
        this.inputSucCtrl = false
        this.isDateValid = true
        return
      }
      if (pattern.test(this.dateInput) === false) {
        this.inputErrCtrl = true
        this.inputSucCtrl = false
        this.isDateValid = false
      } else {
        this.inputErrCtrl = false
        this.inputSucCtrl = true
        this.isDateValid = true
      }
    },
    submit: function () {
      if (this.$store.getters.isLogin === false) {
        this.message = 'Please sign in first!'
        this.flashVisualCtrl = true
        return
      }
      let _this = this
      let token = _this.$store.getters.getToken
      // let formData = {'token': token}
      $.ajax({
        url: '/api/token-verify',
        type: 'POST',
        headers: {
          'X-CSRFToken': $.cookie('csrftoken'),
          'Authorization': token
        },
        // data: JSON.stringify(formData),
        dataType: 'JSON',
        processData: false,
        contentType: 'application/json; charset=utf-8',
        success: function (args) {
          console.log(args)
          if (args.status) {
            _this.dateHandler()
          } else {
            alert('Token is expired or invalid, please sign in again.')
            _this.$store.commit('$_removeStorage')
            window.location.href = '/user/signin'
          }
        }
      })
      _this.btnEnableCtrl = false
      _this.btnValueCtrl = '查询中...'
    },
    hideFlash: function () {
      this.flashVisualCtrl = false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped src="../assets/css/home.css">

</style>



